.App {
   width: 100vw;
   height: 100vh;
   display: flex;
}

body {
  margin: 0;
  padding: 0;
}

.Sidebar {
  height: 100%;
  width: 250px;
  background-color: rgb(93, 125, 125);
}

.SidebarList {
  height: auto;
  padding: 0;
  width: 100%;
}

.SidebarList .row {
  width: 100%;
  height: 80px;
  list-style-type: none;
  margin: 0%;
  display: flex;
  flex-direction: row;
  color: white;
  justify-content: center;
  align-items: center;
  font-family: Georgia, 'Times New Roman', Times, serif;

}

.SidebarList .row:hover {
  cursor: pointer;
  background-color: rgb(29, 88, 88);
}

.SidebarList #active {
  background-color: rgb(29, 88, 88);

}

.row #icon {
  flex: 30%;
  display: grid;
  place-items: center;

}

.row #title {
  flex: 70%;

}

.PageContent {
  flex: 1;
  padding: 20px;
}

.Title {
  font-size:x-large;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.T2 {
  font-size: xx-large;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-align: center;
}